@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  background-color: black;
}



.aboutMe{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin-top: 50px;
    width: 100%;
    background: black;
}


.column
{
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
    color: white;
}

#leftSide
{
    display: flex;
    height: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.aboutMeText{
    color: white;
    align-self: center;
    justify-self: center;
    font-size: 40px;
    font-weight: 900;
    font-family: 'Bebas Neue', cursive;
    font-weight: lighter;
    margin-bottom: -20px;
}

.aboutImg{
    content: url(/online-portfolio/static/media/clip2.37a111ed.png);
    width: 160px;
    height: 160px;
}

.rightSide{
    display: flex;
    height: 100%;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.aboutText{
    width: 50%;
}

.line{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
    align-self: flex-end;
    margin-top: 10px;
}

.deets{
    display: flex;
    height: 100%;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    align-self: flex-start;
}

.vertLine{
    background-color: #909090;
    border: none;
}
@keyframes flickerAnimation {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }

@keyframes move1 {

    0%{
        bottom: 50%;
        right: 50%;
    }

    50%
    {
        bottom: 50%;
        right: 50%;
    }

    100%
    {
        bottom: 0px;
        right: 0px;
    }

}

@keyframes move2 {

    0%{
        top: 50%;
        left: 50%;
    }

    50%
    {
        top: 50%;
        left: 50%;
    }

    100%{
        top: 0px;
        left: 0px;
    }

}

@keyframes move3 {
    0%{
        top: 50%;
        right: 50%;
    }

    50%
    {
        top: 50%;
        right: 50%;
    }

    100%{
        top: 0px;
        right: 0px;
    }
}

@keyframes move4 {
    0%{
        bottom: 50%;
        left: 50%;
    }

    50%
    {
        bottom: 50%;
        left: 50%;
    }

    100%{
        bottom: 0px;
        left: 0px;
    }
}

.landing
{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    height: 90vh;
    width: 100%;
    background: black;
}

.column
{
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
    color: white;
}


#leftSide
{
    height: 100%;
    color: black;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

#rightSide{
    display: flex;
    height: 100%;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

h3{
    align-self: center;
    font-weight: 900;
}

h1{
    align-self: center;
    justify-self: center;
    font-size: 40px;
    font-weight: 900;
    font-family: 'Bebas Neue', cursive;
    font-weight: lighter;
}

label{
    align-self: center;
    justify-self: center;
    font-size: 15px;
    color: #909090;
    font-family: 'Bebas Neue', cursive;
    font-weight: lighter;
}

.plusSignR{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 15px;
    height: 15px;
    padding-right: 30px;
    padding-bottom: 30px;
    animation: move1 2s cubic-bezier(.25,.1,.25,1);
}

.plusSignL
{
    position: absolute;
    top: 0;
    left: 0;
    width: 15px;
    height: 15px;
    padding-left: 30px;
    padding-top: 90px;
    animation: move2 2s cubic-bezier(.25,.1,.25,1);
}

.plusSignRT
{
    position: absolute;
    top: 0;
    right: 0;
    padding-right: 30px;
    padding-top: 90px;
    width: 15px;
    height: 15px;
    animation: move3 2s cubic-bezier(.25,.1,.25,1);
}

.plusSignLB
{
    position: absolute;
    bottom: 0;
    left: 0;
    padding-left: 30px;
    padding-bottom: 30px;
    width: 15px;
    height: 15px;
    animation: move4 2s cubic-bezier(.25,.1,.25,1);
}

.xSign{
    animation: flickerAnimation .15s ease-in 5;
}
.navbar{
    width: 100%;
    height: 10vh;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-content: flex-end;
    background: black;
    padding-bottom: 30px;
    padding-top: 30px;
}

.scrolled{
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
}

.navigation{
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: center;
    font-family: 'Bebas Neue', cursive;
}

.navA{
    font-size: 17px;
    color: orange;
    text-decoration: none;
    font-weight: lighter;
    padding: 5px;
    transition: background-color 0.5s ease;
}

.navA:hover{
    color: black;
    background-color: orange;
}

.navElements{
    margin-right: 50px;
}


.logoimg{
    content: url("/online-portfolio/static/media/ka glow orange.bf8dfa4d.png");
    width: 40px;
    height: 40px;
    padding-left: 30px;
}
